import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import articles from './article-content';
import NotFoundPage from './NotFoundPage';

const ArticlePage = () => {
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
    const [author, setAuthor] = useState('');
    const [text, setText] = useState('');
     
    const { name } = useParams();

    useEffect(() => {
        const loadArticleInfo = async () => {
            try {
                const response = await axios.get(`/articles/${name}`);
                setArticleInfo(response.data);
            } catch (e) {
                console.log(e);
            }
        }

        loadArticleInfo();
    }, [name]);

    const matchingArticle = articles.find(
        article => article.name === name,
    );

    if (!matchingArticle) {
        return <NotFoundPage />
    }

    const addUpvote = async () => {
        const response = await axios.put(`/articles/${name}/upvotes`);
        const updatedArticle = response.data;
        setArticleInfo(updatedArticle);
    }

    const addComment = async () => {
        const response = await axios.post(`/articles/${name}/comments`, {
            author,
            text,
        });
        setArticleInfo(response.data);
        setAuthor('');
        setText('');
    }

    return (
        <>
        <h1>{matchingArticle.title}</h1>
        <div id="upvotes-section">
            <button onClick={addUpvote}>Add Upvote</button>
            <p>This article has {articleInfo.upvotes} upvotes</p>
        </div>
        {matchingArticle.content.map(paragraph => (
            <p key={paragraph}>{paragraph}</p>
        ))}
        <div id="add-comment-form">
            <h3>Add A Comment</h3>
            <label>
                Name:
                <input value={author} onChange={e => setAuthor(e.target.value)} />
            </label>
            <label>
                Comment:
                <textarea row="4" cols="50" value={text} onChange={e => setText(e.target.value)} />
            </label>
            <button onClick={addComment}>Add Comment</button>
        </div>
        <h3>Comments:</h3>
        {articleInfo.comments.map(comment => (
            <div className="comment">
                <h4>{comment.author}</h4>
                <p>{comment.text}</p>
            </div>
        ))}
        </>
    );
}

export default ArticlePage;